<template>
  <Navbar :lang="lang" @changeLang="changeLang" @search="search = $event" :header="header" @header="header = $event" />
  <Notes :lang="lang" :notes="notesSearch" @changeNote="changeNote" @delNote="delNote" />
  <AddButton @openModal="openModal" />
  <Modal :lang="lang" v-if="modalOpen" @closeModal="closeModal" :edit="edit" @addNote="addNote" :editNote="editNote"
    @editedNote="editedNote" />
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import AddButton from "@/components/AddButton.vue";
import Modal from "@/components/Modal.vue";
import lang from '@/lang'
import { v4 as uuidv4 } from "uuid";

export default {
  name: "App",
  components: {
    Navbar,
    Notes,
    AddButton,
    Modal,
  },
  data() {
    return {
      lang: "uz",
      header: true,
      modalOpen: false,
      edit: false,
      notes: [],
      editNote: {},
      langWords: null,
      search: "",
    };
  },
  methods: {
    openModal(value) {
      this.modalOpen = value;
      this.edit = false;
    },
    closeModal(value) {
      this.edit = this.modalOpen = value;
    },
    changeNote(id) {
      this.edit = this.modalOpen = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
    },
    addNote(item) {
      if (item.title != "" && item.descr != "") {
        this.notes.push(item);
      }
    },
    editedNote(item) {
      this.notes.forEach((note) => {
        if (note.id == item.id) {
          note.title = item.title;
          note.descr = item.descr;
          note.date = item.date;
        }
      });
    },
    delNote(id) {
      for (let i = 0; i < this.notes.length; i++) {
        if (this.notes[i].id == id) {
          this.notes.splice(i, 1);
          break;
        }
      }
    },
    getNotes() {
      const localNotes = localStorage.getItem("notes");
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    changeLang(value) {
      this.lang = localStorage.lang = value;
    }
  },
  computed:{
    notesSearch(){
      return this.search ? this.notes.filter((item => item.title.toLowerCase().includes(this.search.toLocaleLowerCase()))) : this.notes
    },
  },
  created() {
    this.getNotes();
    localStorage.lang = localStorage.lang || "uz";
    this.lang = localStorage.lang || "uz";
    this.langWords = lang;
    localStorage.words = JSON.stringify(this.langWords);
  },
  watch: {
    notes: {
      handler(value) {
        localStorage.setItem("notes", JSON.stringify(this.notes));
      },
      deep: true,
    },
  },
};
</script>

