<template>
  <div class="notes">
    <div class="container">
      <div class="notes-top">
        <h2 class="notes-top__title">{{ words.notes.title[lang] }}</h2>
        <button class="notes-top__button" @click="grid = !grid">
          <i class="fas fa-list" v-if="grid"></i>
          <i class="far fa-th" v-else></i>
          <span>{{ grid ? words.notes.list[lang] : words.notes.grid[lang] }}</span>
        </button>
      </div>
      <div class="notes-list" :class="{ grid: !grid }">
        <NotesItem :lang="lang" :grid="grid" v-for="note in notes" :key="note.id" :note="note"
          @changeNote="$emit('changeNote', note.id)" @delNote="$emit('delNote', note.id)" />
      </div>
    </div>
  </div>
</template>

<script>
import NotesItem from "@/components/NotesItem.vue";
export default {
  components: {
    NotesItem,
  },
  props: {
    notes: Array,
    lang: String,
  },
  created() {
    this.words = JSON.parse(localStorage.words)
  },
  data() {
    return {
      grid: true,
      words: null,
    };
  },
};
</script>
