<template>
  <div class="notes-item">
    <div class="notes-item__top" :class="{ between: !grid }">
      <h3 class="notes-item__title">{{ note.title }}</h3>
      <p class="notes-item__date">{{ note.date }}</p>
    </div>
    <p class="notes-item__desc">{{ note.descr }}</p>
    <div class="notes-item__btns">
      <button
        class="notes-item__btn notes-item__btn-edit"
        @click="$emit('changeNote', note.id)"
      >
        <i class="fas fa-pencil"></i>
        <span>{{ words.notesItem.span[lang] }}</span>
      </button>
      <button
        class="notes-item__btn notes-item__btn-delete"
        @click="$emit('delNote', note.id)"
      >
        <i class="fas fa-trash-alt"></i>
        <span>{{ words.notesItem.delete[lang] }}</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    grid: Boolean,
    note: "Object",
    lang: String,
  },
  created() {
    this.words = JSON.parse(localStorage.words)
  },
  data() {
    return {
      words: null,
    };
  },
};
</script>

<style lang="scss" scoped></style>
